@keyframes drop {
  10% {
    opacity: 0.5;
  }
  20% {
    opacity: 1;
    top: 3.78em;
    transform: rotateX(-360deg);
  }
  80% {
    opacity: 1;
    top: 3.78em;
    transform: rotateX(-360deg);
  }
  90% {
    opacity: 0.5;
  }
  100% {
    opacity: 0;
    top: 6.94em;
  }
}
.loading-container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.loading-container h1 {
  margin: 0;
  padding: 0;
  font-weight: bold;
  font-size: 2.2rem;
  color: #f3f7fe;
}
.loading-container h1 span {
  position: relative;
  top: 0.63em;
  display: inline-block;
  text-transform: uppercase;
  opacity: 0;
  transform: rotateX(-90deg);
}
.loading-container .let1 {
  animation: drop 1.2s ease-in-out infinite;
  animation-delay: 1.2s;
}
.loading-container .let2 {
  animation: drop 1.2s ease-in-out infinite;
  animation-delay: 1.3s;
}
.loading-container .let3 {
  animation: drop 1.2s ease-in-out infinite;
  animation-delay: 1.4s;
}
.loading-container .let4 {
  animation: drop 1.2s ease-in-out infinite;
  animation-delay: 1.5s;
}
.loading-container .let5 {
  animation: drop 1.2s ease-in-out infinite;
  animation-delay: 1.6s;
}
.loading-container .let6 {
  animation: drop 1.2s ease-in-out infinite;
  animation-delay: 1.7s;
}
.loading-container .let7 {
  animation: drop 1.2s ease-in-out infinite;
  animation-delay: 1.8s;
}

